<template>
	<el-container class="page-wrapper">
		<el-header>
			<el-button type="text" style="float: right" @click="logout">退出</el-button>
			<h1>思途管理系统 - v1.0</h1>
		</el-header>
		<el-container>
			<el-aside>
				<!-- 菜单项中的index的值藏的是功能组件映射图中的'键' -->
				<el-menu
					:default-active="activeKey"
					@select="menuSelectHandler"
					 background-color="#545c64"
				         text-color="#fff"
				         active-text-color="#ffd04b">
					<template v-for="item in getFuncList(0)">
						<el-submenu v-if="item.func_key === ''" :index="item.func_id.toString()">
							<template slot="title">
								<i class="el-icon-setting"></i>
								<span v-text="item.func_name"></span>
							</template>
							<el-menu-item v-for="item2 in getFuncList(item.func_id)" :key="item2.func_id" :index="item2.func_key">
								<i class="el-icon-menu"></i>
								<span v-text="item2.func_name"></span>
							</el-menu-item>
						</el-submenu>
						<el-menu-item v-else :index="item.func_key">
							<i class="el-icon-menu"></i>
							<span v-text="item.func_name"></span>
						</el-menu-item>
					</template>
				</el-menu>
			</el-aside>
			<el-main>
				<!--
					我： 有功能需求，我就在views文件夹开发对应的组件，我不应该关心这个功能对应哪 个菜单项，我不关心这个功能在菜单中对应的名字（能分析出哪些事情你要管，哪些事情不能管）
					思途的系统管理员： 这个新功能要对应菜单的哪 一项？菜单的名字叫什么？点这个菜单新增tab的title是什么？
					Func: 系统的使用方的管理员可以使用这个组件对整 个系统的菜单进行设计维护，并动态指定菜单关联的功能组件
				-->
				<el-tabs type="card" closable v-model="activeKey" @tab-remove="tabRemoveHandler">
					<el-tab-pane v-for="item in tabs" :key="item" :label="getFuncNameByKey(item)" :name="item">
						<component :is="Views[item].component"></component>
					</el-tab-pane>
				</el-tabs>
			</el-main>
		</el-container>
		<el-footer height="30px">
			<p>&copy;版权所有： 思途191202H5全体同学</p>
		</el-footer>
	</el-container>
</template>

<script type="text/ecmascript-6">
	import Views from '../../views';
	// 我这个被keep-alive缓存的组件怎么判断，什么时候判断，当前登录的用户是不是前面那个因为超时被T出去的用户
	// 如果是同一个用户，我什么也不做
	// 如果不是同一个用户，我得重置我这个页面（把选项卡清空、重新获取菜单再加载）
        export default {
                name: 'Home',
	        data: function() {
                        return {
                                Views,
				tabs: [],
                                // 是联动左边菜单和右边选项卡的关键数据
	                        // 右功两边选中效果的同步，全靠这个数据来驱动
	                        activeKey: '',
	                        userFuncList: []                        // 保存当前登录的用户的角色对应的功能列表
                        };
	        },
	        computed: {
                        getFuncList: function() {
                                return func_fid => this.userFuncList.filter(item => item.func_fid === func_fid);
                        },
		        getFuncNameByKey() {
                                return func_key => this.userFuncList.find(item => item.func_key === func_key).func_name;
		        }
	        },
	        methods: {
                        logout() {
                                sessionStorage.clear();
                                this.$message({ message: `退出登录 成功！`, type: 'success' });
                                this.$router.replace('/login');
                        },
                        menuSelectHandler: function(index) {
                                this.activeKey = index;
	                        if(this.tabs.indexOf(index) === -1) this.tabs.push(index);
                        },
		        tabRemoveHandler: function(name) {
				var i = this.tabs.indexOf(name);
				this.tabs.splice(i, 1);
				// 如果删除的tab正好是处于激活的，那么让剩下的tab中的最后一个激活，如果删没了，activeKey重置成''空字符串
				if(this.activeKey === name) this.activeKey = this.tabs[this.tabs.length - 1] || '';
		        }
	        },
	        // 从不存在到创建变成存在才会调用，但是存在以后就不会调用了
	        created: function() {},
	        // 从不存在到存在会调用，存在以后从没激活到激活也会调用
	        activated() {
                        // 如果是相同的用户登录，直接返回什么也不干
		        if(sessionStorage.getItem('prev_user') === sessionStorage.getItem('user')) return;
		        // 能通过上面那个if判断，到下面说明一定是一个新的用户登录了
                        // 重置数据
		        this.activeKey = '';
		        this.tabs = [];
		        this.userFuncList = [];
		        // 再次为新登录的用户发送请求
                        // 通知字典表仓库准备系统基础字典相关数据
                        this.$store.dispatch('dictionary/init');
                        // 发送ajax到服务器拿取：当前登录的用户的角色对应的功能列表
                        this.$http({ url: '/user/getmenu', method: 'post' }).then(data => this.userFuncList = data);
	        },
	        deactivated() {}
        };
</script>

<style scoped>
	.page-wrapper { height: 100%; }
	.el-header { background-color: #303133; }
	.el-header>h1 { color: #fff; height: 60px; line-height: 60px; }

	.el-aside { background-color: #545c64; }
	.el-menu { border-right: none; }

	.el-footer { background-color: #C0C4CC; }
	.el-footer>p { font-size: 12px; height: 30px; line-height: 30px; text-align: center; }


	.el-tabs { height: 100%; display: flex; flex-direction: column; }
	/* 父组件如何控制子组件内部渲染的元素的样式 */
	.el-tabs >>> .el-tabs__header { flex-shrink: 0; }
	.el-tabs /deep/ .el-tabs__content { flex-grow: 1; overflow: auto; }
	.el-tab-pane { height: 100%; }
</style>