<template>
	<el-container>
		<el-header height="40px">
			<el-form :inline="true">
				<el-form-item label="员工名字：">
					<el-input
						v-model.trim="search.stf_name"
						placeholder="请输入搜索内容"
						clearable
						@change="getData()"
						prefix-icon="el-icon-search"></el-input>
				</el-form-item>
				<el-form-item label="员工类型：">
					<el-select v-model="search.stf_category" @change="getData()">
						<i slot="prefix" class="el-icon-search"></i>
						<el-option :value="0" label="- 所有类型 -"></el-option>
						<el-option
							v-for="item in $store.getters['dictionary/getGroupByKey']('staff_category')"
							:key="item.dic_id"
							:value="item.dic_id"
							:label="item.dic_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item style="float: right">
					<el-button type="success" size="medium" icon="el-icon-plus" @click="beginAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-header>
		<el-main>
			<!-- 用elementui中的table组件进行渲染、自己设计表格的增删改功能实现 -->
			<div class="table-wrapper">
				<el-table border stripe :highlight-current-row="true" :data="list" height="100%">
					<el-table-column type="index" label="#" align="center" fixed></el-table-column>
					<el-table-column label="姓名" prop="stf_name" width="100" fixed></el-table-column>
					<el-table-column label="职务类型" prop="stf_category" width="120" align="right">
						<template slot-scope="{ row }">
							<span v-text="$store.getters['dictionary/getNameById'](row.stf_category)"></span>
						</template>
					</el-table-column>
					<el-table-column label="备注" prop="stf_remark" >
						<template slot-scope="{ row }">
							<span :class="{empty : !row.stf_remark}" v-text="row.stf_remark || '暂无相关备注...' "></span>
						</template>
					</el-table-column>
					<el-table-column label="员工状态" prop="stf_invalid" width="90" align="center">
						<template slot-scope="{ row }">
							<span :class="{dimission : !row.stf_invalid}" v-text="row.stf_invalid ? '在职' : '离职' "></span>
						</template>
					</el-table-column>
					<el-table-column label="操作" width="180" align="center" fixed="right">
						<template slot-scope="{ row }">
							<el-button type="primary" size="mini" @click="beginUpdate(row)">编辑</el-button>
							<el-button type="danger" size="mini" v-if="row.stf_invalid" @click="dimissionHandler(row)">离职</el-button>
							<el-button type="warning" size="mini" v-else @click="reinstateHandler(row)">入职</el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-main>
		<el-footer height="32px">
			<el-pagination
				background
				:total="pagination.total"
				:page-size.sync="pagination.pageSize"
				:current-page.sync="pagination.currentPage"
				layout="total, prev, pager, next, jumper, ->, sizes"
				:page-sizes="[5, 8, 10, 12, 15]"
				@size-change="getData()"
				@current-change="getData"
			></el-pagination>
		</el-footer>
		<!-- 编辑对话框 -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit.isEdit"
			:show-close="false">
			<h2 slot="title" v-text="`员工维护 - ${ edit.mode ? '新增' : '修改' }`" class="custom-dialog-title"></h2>
			<el-form label-width="100px" :model="edit.model" :rules="edit.validationRules" ref="editForm" status-icon>
				<el-form-item label="员工名字："  prop="stf_name">
					<el-input v-model.trim="edit.model.stf_name" placeholder="请输入员工的名字"></el-input>
				</el-form-item>
				<el-form-item label="员工类型：" prop="stf_category">
					<el-select v-model="edit.model.stf_category" placeholder="- 请选择员工类型 -">
						<el-option
							v-for="item in $store.getters['dictionary/getGroupByKey']('staff_category')"
							:key="item.dic_id"
							:value="item.dic_id"
							:label="item.dic_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="员工备注：">
					<el-input type="textarea" :rows="4" placeholder="请输入员工备注.." v-model.trim="edit.model.stf_remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" @click="save" size="medium">保存</el-button>
				<el-button @click="edit.isEdit = false" size="medium">取消</el-button>
			</div>
		</el-dialog>
	</el-container>
</template>

<script type="text/ecmascript-6">
        export default {
                name: 'Staff',
                data() {
                        return {
	                        search: { stf_name: '', stf_category: 0 },
	                        pagination: { total: 0, pageSize: 5, currentPage: 1 },
	                        edit: {
	                                isEdit: false,
		                        mode: true,
		                        model: { stf_id: 0, stf_name: '', stf_category: '', stf_remark: ''  },
                                        validationRules: {
						stf_name: [
							{ required: true, message: '员工名字不能为空', trigger: 'blur' },
							{ min: 2, max: 20, message: '员工名字长度 2 - 20', trigger: 'blur' }
						],
	                                        stf_category: [
		                                        { required: true, message: '请选择员工的类型', trigger: 'change' }
	                                        ]
                                        }
	                        },
                                list: []                        // 当前正在显示的员工信息
                        };
                },
	        created() { this.getData(); },
	        methods: {
                        getData(currentPage = 1) {
                                this.pagination.currentPage = currentPage;
                                // 向服务器发送ajax请求，获取数据，并更新到data节点的list中
				this.$http({
                                        url: '/staff/list',
                                        method: 'post',
                                        data: {
                                                ...this.search,
                                                begin: (this.pagination.currentPage - 1) * this.pagination.pageSize,
                                                pageSize: this.pagination.pageSize
                                        }
                                }).then(({ list, total }) => {
                                        this.list = list;
                                        this.pagination.total = total;
                                });
                        },
		        // 离职
		        dimissionHandler(staff) {
                                this.$confirm(
                                        `确定要让员工："${ staff.stf_name }" 离职吗？`,
                                        '警告',
                                        { showClose: false,  type: 'warning' }
                                )
	                                .then(() => this.$http({ url: `/staff/dimission/${ staff.stf_id }`, method: 'get' }))
	                                .then(() => {
                                                var i = this.list.findIndex(item => item.stf_id === staff.stf_id);
                                                this.list[i].stf_invalid = 0;
                                                this.$message({ message: `员工："${ staff.stf_name }" 离职成功！`, type: 'success' });
	                                })
	                                .catch(() => {});
		        },
		        // 入职
		        reinstateHandler(staff) {
                                this.$confirm(
                                        `确定要让员工："${ staff.stf_name }" 复职吗？`,
                                        '警告',
                                        { showClose: false,  type: 'warning' }
                                )
	                                .then(() => this.$http({ url: `/staff/reinstate/${ staff.stf_id }`, method: 'get' }))
	                                .then(() => {
                                                var i = this.list.findIndex(item => item.stf_id === staff.stf_id);
                                                this.list[i].stf_invalid = 1;
                                                this.$message({ message: `员工："${ staff.stf_name }" 复职成功`, type: 'success' });
	                                })
	                                .catch(() => {});
		        },
		        // 开始新增
		        beginAdd() {
				this.edit.model.stf_id = 0;
				this.edit.model.stf_name = '';
				this.edit.model.stf_category = '';
				this.edit.model.stf_remark = '';

				this.edit.mode = true;
				this.edit.isEdit = true;
		        },
		        // 开始修改
		        beginUpdate(staff) {
                                this.edit.model.stf_id = staff.stf_id;
                                this.edit.model.stf_name = staff.stf_name;
                                this.edit.model.stf_category = staff.stf_category;
				this.edit.model.stf_remark = staff.stf_remark;

			        this.edit.mode = false;
				this.edit.isEdit = true;
		        },
		        _saveWithAdd() {
                                this.$http({ url: '/staff/add', method: 'post', data: this.edit.model })
                                        .then(stf_id => {
                                                this.edit.isEdit = false;
                                                this.list.push(Object.assign({}, this.edit.model, { stf_id }));
                                                this.$message({
                                                        message: `员工："${ this.edit.model.stf_name }" 添加成功！`,
                                                        type: 'success'
                                                });
                                        });
		        },
		        _saveWithUpdate() {
                                this.$http({ url: '/staff/update', method: 'post', data: this.edit.model })
                                        .then(() => {
                                                this.edit.isEdit = false;
                                                var i = this.list.findIndex(item => item.stf_id === this.edit.model.stf_id);
                                                this.list.splice(i, 1, Object.assign({}, this.edit.model));
                                                this.$message({
                                                        message: `员工："${ this.edit.model.stf_name }" 修改成功！`,
                                                        type: 'success'
                                                });
                                        });
		        },
		        // 保存（新增/修改）
		        save() {
                                this.$refs.editForm.validate(isValid => {       // 代码触发表单验证
                                        if(isValid) {
                                                // 新增
                                                if(this.edit.mode) this._saveWithAdd();
                                                // 修改
                                                else this._saveWithUpdate();
                                        }
                                });
		        }
	        },
	        watch: {
                        'edit.isEdit'(newValue, oldValue) {
                                if(!newValue) this.$refs.editForm.resetFields();
                        }
	        }
        };
</script>

<style scoped>
	.el-container { height: 100%; }
	.el-main { position: relative; }
	.el-main>.table-wrapper {
		box-sizing: border-box;
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0; top: 0;
		padding: 10px;
		overflow: hidden;
	}


	span.dimission { color: red; }
	span.empty { color: #ccc; }

	h2.custom-dialog-title {
		background-color: #0094ff;
		text-indent: 2em;
		color: #fff;
		height: 40px;
		line-height: 40px;
		font-size: 18px;
	}


	/*!important*/
	/*行内样式*/
	/*选择器的权重*/
	/*权重相同，看样式出现的顺序，后出现的优先级高*/
</style>