<template>
	<el-container>
		<el-header height="20px">
			<span class="state free"></span> 闲置
			<span class="state occupy"></span> 使用中
		</el-header>
		<el-main>
			<el-row :gutter="20">
				<transition-group name="edit">
					<el-col :span="6" v-for="item in $store.state.classroom.list" :key="item.clsr_id">
						<el-card
							:class="item.clsr_occupy ? 'occupy' : 'free'"
							@mouseover.native="hoverId = item.clsr_id"
							@mouseout.native="hoverId = 0">
							<span v-text="item.clsr_name"></span>
							<div class="mask" v-show="item.clsr_id === hoverId">
								<i class="el-icon-edit" @click="beginUpdate(item)"></i>
								<i class="el-icon-delete" @click="removeHandler(item)" v-if="!item.clsr_occupy"></i>
							</div>
						</el-card>
					</el-col>
				</transition-group>
				<el-col :span="6">
					<el-card @click.native="beginAdd">
						<i class="el-icon-plus"></i>
					</el-card>
				</el-col>
			</el-row>
		</el-main>
		<!-- 编辑对话框 -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit.isEdit"
			:show-close="false">
			<h2 slot="title" v-text="`教室管理 - ${ edit.mode ? '新增' : '修改' }`" class="custom-dialog-title"></h2>
			<el-form label-width="100px" :model="edit.model" :rules="edit.validationRules" ref="editForm">
				<el-form-item label="教室名字："  prop="clsr_name">
					<el-input v-model.trim="edit.model.clsr_name" placeholder="请输入教室名字"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" @click="save">保存</el-button>
				<el-button @click="edit.isEdit = false">取消</el-button>
			</div>
		</el-dialog>
	</el-container>
</template>

<script type="text/ecmascript-6">
        export default {
                name: 'Classroom',
                data() {
                        return {
                                hoverId: 0,                     // 记录当前正被鼠标滑过的教的id
                                edit: {
                                        isEdit: false,
                                        mode: true,
                                        model: { clsr_id: 0, clsr_name: '' },
                                        validationRules: {
                                                clsr_name: [
                                                        {
                                                                validator: (rule, value, callback) => {
									if(value === '')
									        callback(new Error('教室名不能为空'));
									else if(value.length < 2 || value.length > 20)
									        callback(new Error('教室名长度 2 - 20'));
									// 在list中是不是存在id和当前编辑的id不同，而名字又相同的元素，如果有就说明名字冲突（这个逻辑同时适用于新增和修改）
									else if(this.$store.state.classroom.list.some(item => item.clsr_id !== this.edit.model.clsr_id && item.clsr_name === value))
									        callback(new Error('教室名已存在'));
	                                                                else
	                                                                        callback();
                                                                },
                                                                trigger: 'blur'
                                                        }
                                                ]
                                        }
                                }
                        };
                },
                created() { this.$store.dispatch('classroom/init'); },
                methods: {
                        // 删除
                        removeHandler: function(classroom) {
                                this.$confirm(
                                        `确定要删除教室："${ classroom.clsr_name }" ？`,
                                        '警告',
                                        { showClose: false,  type: 'warning' }
                                )
                                // 调用classroom子仓库中的actions节点中的remove方法
                                        .then(() => this.$store.dispatch('classroom/remove', classroom.clsr_id))
                                        // 收尾性的工作：提示删除成功
                                        .then(() => this.$message({ message: `教室："${ classroom.clsr_name }" 删除成功！`, type: 'success' }))
                                        .catch(() => {});
                        },
                        // 开始新增
                        beginAdd() {
                                this.edit.model.clsr_id = 0;
                                this.edit.model.clsr_name = '';

                                this.edit.mode = true;
                                this.edit.isEdit = true;
                        },
                        // 开始修改
                        beginUpdate(classroom) {
                                this.edit.model.clsr_id = classroom.clsr_id;
                                this.edit.model.clsr_name = classroom.clsr_name;

                                this.edit.mode = false;
                                this.edit.isEdit = true;
                        },
                        _saveWithAdd() {
                                this.$store.dispatch('classroom/add', this.edit.model)
                                        .then(() => {
                                                this.edit.isEdit = false;
                                                this.$message({
                                                        message: `教室："${ this.edit.model.clsr_name }" 添加成功！`,
                                                        type: 'success'
                                                });
                                        });
                        },
                        _saveWithUpdate() {
                                this.$store.dispatch('classroom/update', this.edit.model)
                                        .then(() => {
                                                this.edit.isEdit = false;
                                                this.$message({
                                                        message: `教室："${ this.edit.model.clsr_name }" 修改成功！`,
                                                        type: 'success'
                                                });
                                        });
                        },
                        // 保存（新增/修改）
                        save() {
                                this.$refs.editForm.validate(isValid => {       // 代码触发表单验证
                                        if(isValid) {
                                                // 新增
                                                if(this.edit.mode) this._saveWithAdd();
                                                // 修改
                                                else this._saveWithUpdate();
                                        }
                                });
                        }
                },
                watch: {
                        'edit.isEdit'(newValue, oldValue) {
                                if(!newValue) this.$refs.editForm.resetFields();
                        }
                }
        };
</script>

<style scoped>
	.el-container { height: 100%; position: relative; }

	span.state {
		vertical-align: middle;
		display: inline-block;
		width: 20px;
		height: 20px;
		margin-left: 20px;
	}
	.free { background-color: #67C23A; }
	.occupy { background-color: #E6A23C; }

	.el-col { margin-bottom: 20px; }
	.el-card {
		height: 120px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
	}
	.el-card span { color: #fff; font-size: 20px; }
	.mask {
		position: absolute; left: 0; top: 0;
		width: 100%; height: 100%;
		background-color: rgba(0,0,0,0.6);
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.mask>i {
		font-size: 40px;
		color: #fff;
		margin: 0 10px;
		cursor: pointer;
	}


	i { font-size: 30px; }

	h2.custom-dialog-title {
		background-color: #0094ff;
		text-indent: 2em;
		color: #fff;
		height: 40px;
		line-height: 40px;
		font-size: 18px;
	}

	.edit-enter, .edit-leave-to { opacity: 0; }
	.edit-enter-active, .edit-leave-active { transition: all 1s ease-in 0.5s; }
</style>